import React, { Component } from "react";
import { BrowserRouter, Route, Link } from "react-router-dom";
import { createStore } from "redux";
import Home from "./Home";
import Logo from './imgs/logo.png';

const App = () => (
  <BrowserRouter>
    <div className="App">
      <Nav></Nav>
      <div>
      <Route exact path="/" component={Home} />
      <Route path="/member" component={Member} />
      <Route path="/strategy" component={Strategy} />
      <Route path="/blog/:id" component={Blog}></Route>
      <Route path="/sum/:num1/:num2" component={Sum}></Route>
      </div>
      <Footer></Footer>
    </div>
  </BrowserRouter>
);

class Nav extends React.Component {
  render() {
    return (
      <nav className="navbar navbar-expand-lg navbar-light fixed-top py-3" id="mainNav">
        <div className="container">
          <Link className="navbar-brand js-scroll-trigger" to="#page-top">
            <img src={Logo} width="50" height="50" alt=""/>
            <b>みるく屋本舗</b>
          </Link>
          <button className="navbar-toggler navbar-togglar-right" type="button" data-toggle="collapse"
            data-target="#navbarResponsive" aria-controls="navbarResponsive" aria-expanded="false"
            aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarResponsive">
            <ul className="navbar-nav ml-auto my-2 my-lg-0">
              <li className="nav-item">
                <Link className="nav-link js-scroll-trigger" to="#about">チーム概要</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link js-scroll-trigger" to="/member">メンバー一覧</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link js-scroll-trigger" to="#infomation">今後の予定</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link js-scroll-trigger" to="#news">アルバム</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link js-scroll-trigger" to="#contact">加入したい方は</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="capture.html">攻略本</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="teamform.html">お問い合わせ</Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    );
  }
}

class Footer extends React.Component{
  render(){
    return(
      <footer className="bg-light py-5">
        <div className="container">
          <div className="small text-center text-muted">※当サイトの一部の掲載画像等に関する権利は、株式会社セガゲームズに帰属しています。</div>
          <div className="small text-center text-muted">Copyright &copy; 2019 - MYH / SEGA PHANTASY STAR ONLINE 2</div>
        </div>
      </footer>
    );
  }
}

const vote = (state = 0, action) => {
  switch (action.type) {
    case "ADD":
      return state + 1;
    default:
      return state;
  }
};

let store = createStore(vote);

const Member = () => {
  store.dispatch({ type: "ADD" });
  let x = store.getState().toString();
  return (
    <div>
      <h1>About</h1>
      <p>投票数 : {x}</p>
    </div>
  );
};

const Strategy = () => {
  store.dispatch({ type: "ADD" });
  let x = store.getState().toString();
  return (
    <div>
      <h1>About</h1>
      <p>投票数 : {x}</p>
    </div>
  );
};

const Blog = props => {
  const { id } = props.match.params;
  return (
    <div>
      <p>{id}番目の記事です</p>
    </div>
  );
};

const Sum = props => {
  const { num1, num2 } = props.match.params;
  return (
    <div>
      <p>
        {num1} + {num2} = {parseInt(num1) + parseInt(num2)}
      </p>
    </div>
  );
};
export default App;
