import React, { Component } from "react";
import "./Home.css";

const Home = () => {
  return (
    <div>
      <header classNameName="masthead" id="page-top">
        <div classNameName="container h-100">
          <div classNameName="row h-100 align-items-center justify-content-center text-center">
            <div classNameName="col-lg-10 align-self-end">
              <h1 classNameName="text-white font-weight-bold">ようこそ</h1>
              <hr classNameName="divider my-4"/>
            </div>
            <div classNameName="col-lg-8 align-self-baseline">
              <p classNameName="text-white font-weight-light mb-5" style={{fontSize: "25px"}}>PSO2のチーム「みるく屋本舗」のHPです！</p>

              <div classNameName="text-white mb-3" style={{textAlign: "center"}}>
                <span style={{verticalAlign: "top"}}>訪問者はあなたで</span>
                <table border="0" cellspacing="0" cellpadding="0" style={{display: "inline-block"}}>
                  <tr>
                    <td align="center"><a href="http://www.rays-counter.com/"><img
                          src="http://www.rays-counter.com/d439_f6_159/5d0788d610209/" alt="アクセスカウンター" border="0"/></a></td>
                  </tr>
                  <tr>
                    <td align="center"><img src="http://www.rays-counter.com/images/counter_01.gif" border="0" alt=""/>
                    <img
                        src="http://www.rays-counter.com/images/counter_02.gif" border="0" alt=""/><img
                        src="http://www.rays-counter.com/images/counter_03.gif" border="0" alt=""/><img
                        src="http://www.rays-counter.com/images/counter_04.gif" border="0" alt=""/><img
                        src="http://www.rays-counter.com/images/counter_05.gif" border="0" alt=""/></td>
                  </tr>
                </table>
                <span style={{verticalAlign: "top"}}>人目です！</span>
              </div>
              <a classNameName="btn btn-primary btn-xl js-scroll-trigger" href="#about">どんなチームなの？</a>
            </div>
          </div>
        </div>
      </header> 
   
      <section className="page-section bg-primary" id="about">
        <div className="container">
          <div className="row justify-content-center text-light">
            <div className="col-lg-8 text-center">
              <h2 className=" mt-0"><b>「みるく屋本舗」チーム概要</b></h2>
              <hr className="divider yellow my-4"/>
              <p className="text-white-50 mb-4">メンバーが牧牛のように自由気ままにプレイできる...</p>
              <p className="text-white-50 text-muted mb-4">そんな空間となるように名付けました！</p>
              <p className=" mb-4">設立 : <b>2019年 06月02日</b></p>
              <p className=" mb-4">活動シップ : <b>Ship7 ギョーフ</b></p>
              <p className=" mb-4">活動時間帯 : <b>18:00～0:00</b></p>
              <p className=" mb-4">活動内容 : <b>チムメン交流、協力してクエスト</b></p>
              <p className=" mb-4"><b>不定期ですがチームイベントも開催予定！</b></p>
              <p className=" mb-4 pb-4">チーム人数 : 現在は <b>11</b> 人です！</p>
              <a className="btn btn-light btn-xl js-scroll-trigger" href="#infomation">どんなことしてるの？</a>
            </div>
          </div>
        </div>
      </section>
  
      <section className="page-section bg-dpurple" style={{color: "#dbedf3"}} id="infomation">
        <div className="container text-left">
          <h2 className="mt-0 text-center"><b>今後の予定！</b></h2>
          <hr className="divider light mt-4"/>
          <div className="row justify-content-center">
            <div className="col-lg-8">
              <div className="mt-5 mb-3">
                <h3 className="text-lgreen font-weight-bold">第１回チームイベント開催決定ッ！</h3>
                <span className="badge badge-primary"><i className="fas fa-check"></i>チームイベント</span>
                <span className="badge badge-secondary"><i className="fas fa-pen-square"></i> 2019/06/29</span>
              </div>
              <p>表題の通り、<strong className="lead font-weight-bold">第１回チームイベント開催が決定しました！</strong><br/>
              開催日程は、<strong className="lead font-weight-bold">2019年07月13日(土) 21:00～</strong>です！<br/>
              参加を希望される方は、参加費として100,000メセタが必要になりますが、
              集めたメセタは、イベント内の順位で誰かの手に賞金として渡ります！<b>一攫千金のチャンス！</b></p>
              <div className="card my-3 px-3 text-dark">
                <h3 className="mt-3">参加費のお支払方法</h3>
                <div className="card-body">
                  <ol className=" my-3 py-3">
                    <li>ビジフォンを開いて「マイショップ」を選択する。</li>
                    <li>「チームメンバーのマイショップを開く」より、Ormairのマイショップを選択する。</li>
                    <li>販売しているグラインダーを一つ購入する。</li>
                    <li>参加手続き完了！</li>
                  </ol>
                </div>
              </div>
              <p>メンバー同士の親睦を深める場となり、チームの魅力を存分に感じていただければ幸いです！</p>
              <p>記念すべきみるく屋本舗の最初のチームイベント、是非ふるってご参加ください！！！！</p>
            </div>
          </div>
        </div>
      </section>
    
      <section className="page-section bg-dark text-white" id="news">
        <div className="container text-left">
          <h2 className="mt-0 text-center"><b>こんなことがありました！</b></h2>
          <hr className="divider light mt-4"/>
          <div className="row justify-content-center">
            <div className="col-lg-8">
              <div className="my-4">
                <h3 className="text-lgreen font-weight-bold">第一回チームイベント開催ッ！</h3>
                <span className="badge badge-primary"><i className="fas fa-check"></i>チームイベント</span>
                <span className="badge badge-info"><i className="far fa-calendar-check"></i> 2019/07/13</span>
                <span className="badge badge-secondary"><i className="fas fa-pen-square"></i> 2019/07/15</span>
              </div>
              <p>前々から予告していた通り、チームイベントを開催しました！！</p>
              <p>イベントの様子はばるさんが撮影してくれました！！ありがとう！！</p>
              <div className="my-4">
                <div id="carouselExampleIndicators" className="carousel slide" data-ride="carousel">
                  <ol className="carousel-indicators">
                    <li data-target="#carouselExampleIndicators" data-slide-to="0" className="active"></li>
                    <li data-target="#carouselExampleIndicators" data-slide-to="1"></li>
                    <li data-target="#carouselExampleIndicators" data-slide-to="2"></li>
                  </ol>
                  <div className="carousel-inner">
                    <div className="carousel-item active">
                      <a href="imgs/album03.jpg"><img className="img-thumbnail d-block w-100" src="imgs/album03.jpg" alt="First slide"/></a>
                    </div>
                    <div className="carousel-item">
                      <a href="imgs/album04.jpg"><img className="img-thumbnail d-block w-100" src="imgs/album04.jpg" alt="Second slide"/></a>
                    </div>
                    <div className="carousel-item">
                      <a href="imgs/album05.jpg"><img className="img-thumbnail d-block w-100" src="imgs/album05.jpg" alt="Third slide"/></a>
                    </div>
                  </div>
                  <a className="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
                    <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                    <span className="sr-only">Previous</span>
                  </a>
                  <a className="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
                    <span className="carousel-control-next-icon" aria-hidden="true"></span>
                    <span className="sr-only">Next</span>
                  </a>
                </div>
              </div>
              <p>今回のイベントでは、ミニゲームを交えたすごろくをしました！！</p>
              <p>各メンバーがサイコロを振り、振った目に書いてあるミニゲームを行い、ミニゲームに敗北したときのペナルティを受けた結果だけ進むというものです！</p>
              <p>一枚目は、クイズの様子です！</p>
              <p>今回のクイズのお題は<strong className="lead font-weight-bold">「学校」</strong>と<strong className="lead font-weight-bold">「お菓子」</strong>！！</p>
              <p>出題者として私おるめーると、マネージャーのがーさんとばるさんが協力してくれました！がーさんが出す問題が結構難易度高くて、皆めちゃくちゃ苦戦したよ～...w</p>
              <p>二枚目は、ダーツの様子です！</p>
              <p>ダーツは完全に運ゲーなんですけど、ばるさんが覚醒して圧倒していました！カタナで渋いばるさんですが、侍の集中力は計り知れない...</p>
              <p>三枚目はとことこレースの様子です！</p>
              <p>やったことある方ならわかるかもしれないですけど、これ結構難しいんですよね～。私は経験者でしたが、がーさんやおうせさんに惨敗しました...悔しい！！それと、おうせさんはめちゃくちゃ強くて「とことこ魔人」とか呼ばれていましたw</p>
              <p>本当はこの後に、みんなで仮面行こうと思ってたんですが、予想していた通りそううまくはいかず...終わるころには0時になっていましたw</p>
              <p>なんやかんや色々ありましたけど、<strong className="lead font-weight-bold">みなさん楽しんでいただけました！！</strong></p>
              <a href="imgs/album06.jpg"><img className="img-fluid img-thumbnail my-4" src="imgs/album06.jpg"/></a>
              <p>最後にみんなで記念撮影をしました～</p>
              <p>時間も時間でしたので、もっといい感じのSSを撮影したかったですが、断念...</p>
              <p>今回のすごろくの優勝者は...<strong className="display-4 lead font-weight-bold">おうせさん</strong>でした！！！</p>
              <p>賞品としてブランマーメイドの[Ba]と[Ou]とメセタをお渡ししました！！</p>
              <p>皆さん本当にお疲れさまでした！！次回もよろしくお願いします！！</p>
              <div className="border my-5"></div>
              <div className="my-4">
                <h3 className="text-lgreen font-weight-bold">イェーガーさん入団ッ！</h3>
                <span className="badge badge-primary"><i className="fas fa-check"></i>メンバー入団</span>
                <span className="badge badge-info"><i className="far fa-calendar-check"></i> 2019/06/06</span>
                <span className="badge badge-secondary"><i className="fas fa-pen-square"></i> 2019/06/06</span>
              </div>
              <a href="imgs/album02.jpg"><img className="img-fluid img-thumbnail mb-4" src="imgs/album02.jpg"/></a>
              <p>私たちに新しいメンバーが加わりました～</p>
              <p><strong className="lead font-weight-bold">キャストのイェーガーさんです！</strong></p>
              <p>つい二日前に始めたばかりで、PS4勢の方だとお聞きしました。</p>
              <p>一緒にこのチームを盛り上げていきましょう！</p>
              <p>これからよろしくお願いします！！！</p>
              <div className="border my-5"></div>
              <div className="my-4">
                <h3 className="text-lgreen font-weight-bold">チーム設立ッ！</h3>
                <span className="badge badge-primary"><i className="fas fa-check"></i>原点</span>
                <span className="badge badge-info"><i className="far fa-calendar-check"></i> 2019/06/02</span>
                <span className="badge badge-secondary"><i className="fas fa-pen-square"></i> 2019/06/02</span>
              </div>
              <a href="imgs/album01.jpg"><img className="img-fluid img-thumbnail mb-4" src="imgs/album01.jpg"/></a>
              <p>この度、<strong className="lead font-weight-bold">「みるく屋本舗」</strong>という名前で新たにチームを設立いたしました！</p>
              <p>チームの皆がいろいろな人と交流して、普通にPSO2をしているだけでは経験できない、メンバーとの絆から生まれる楽しさを提供できればと思っています！</p>
              <p>このサイトもまだまだ開設したばかりなので，コンテンツももっと充実させていきたいなー...</p>
              <p>まだまだ小さいチームですが、二人から精一杯頑張っていきます！</p>
            </div>
          </div>
        </div>
      </section>
    
      <section className="page-section h-100" id="contact">
        <div className="container h-100">
          <div className="row h-100 d-flex align-items-center justify-content-center ">
            <div className="col-lg-8 text-center">
              <h2 className="mt-0"><b>チームに入りたい方は...</b></h2>
              <hr className="divider my-4"/>
              <p className="text-muted">下記のページから加入申請が可能です。</p>
              <p className="text-muted">加入に際しまして、リンク先の<mark>チーム内規約を必ずお読みください。</mark></p>
              <a className="btn btn-primary btn-xl mt-3 mb-5" href="rules.html">加入申請はこちら</a>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default Home;